.errorWrapper {
    background: url(https://38.media.tumblr.com/546c0cd48d71f210f9b67a389003790d/tumblr_neq0yw9rMA1tm16jjo1_500.gif)
        no-repeat center center fixed;
    background-size: cover;
    font-family: "Roboto", sans-serif;

    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.error h1 {
    font-size: 7em;
    margin: 0.2em 0.5em;
    color: rgba(255, 255, 255, 0.7);
    margin-bottom: 0px;
}

.error h2 {
    font-size: 1.2em;
    margin: 0.2em 0.5em;
    color: rgba(255, 255, 255, 0.6);
}

.error {
    position: absolute;
    top: 30%;
    margin-top: -8em;
    width: 100%;
    text-align: center;
}
