nav {
    height: 50px !important;
    background-color: black;
    overflow: hidden;
    padding: 0px 32px;
    display: flex;
    justify-content: space-between;
}

.vertical-align {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100% !important;
}

#mobile-nav-icon {
    height: 35px;
    width: 20px;
    color: white;
}

.modal-overlay {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 10;
    overflow: hidden;
    visibility: hidden;
    opacity: 0;
    transition: 0.5s;
}

.ink-wrapper {
    position: relative;
    height: 100%;
    width: 100%;
}

.ink-content {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(0, -50%);
    height: 100%;
    width: 2500%;
    background-image: url("../images/ink.png");
    background-position: 0 0;
    background-size: 100% 100%;
}

.mobile-ul .mobile-li {
    opacity: 0;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 11;
    // background-image: url("../images/stars_and_cloud_bg.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    transition: opacity 1.8s;

    .mobile-nav {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        background-color: rgba(0, 0, 0, 0.95);
        height: 100%;
        left: 0;
        list-style: none;
        margin: 0;
        overflow: auto;
        padding: 15% 7%;
        position: fixed;
        text-align: center;
        top: 0;
        width: 100%;
        z-index: 100;

        &:before {
            content: "";
            display: inline-block;
            position: fixed;
            left: 40px;
            top: 20px;
            z-index: 2;
            background-color: white;
            border-radius: 50%;
            height: 8px;
            width: 8px;
        }

        .close-menu {
            color: #8ab6eb !important;
            // color: #f7299a !important;
            font-size: 22px;
            line-height: 22px;
            position: fixed;
            left: 35px;
            top: 12px;
            z-index: 3 !important;
        }
    }

    .divider {
        width: 100%;
        border-bottom: 1px solid #b6eb8a;
    }

    ul {
        padding: 0;
        margin: 0;

        li {
            height: auto;
            line-height: 30px;
            margin-left: 0;

            &:nth-child(even) {
                a {
                    color: #eb8ab6 !important;
                    // color: #efa2b0 !important;
                    // color: #f7299a !important;
                }
            }

            a {
                font-family: FancyHeart;
                font-size: 21px;
                display: block;
                padding: 15px 0;
                color: #fff4fe;
            }
        }
    }
}

#toggleMenu {
    display: none;
}

#toggleMenu:checked ~ .modal-overlay {
    visibility: visible;
    opacity: 1;
}

#toggleMenu:checked ~ .modal {
    transition-delay: 0.4s;
}

#toggleMenu:checked ~ .modal-overlay .ink-content {
    animation: sequence 0.7s steps(24);
    animation-fill-mode: forwards;
}

// #toggleMenu:checked ~ .modal {
//     visibility: visible;
//     opacity: 1;
// }

#toggleMenu:not(:checked) ~ .modal-overlay.close {
    visibility: hidden;
    opacity: 0;
    transition-delay: 0.5s;
}

#toggleMenu:not(:checked) ~ .modal-overlay.close .ink-content {
    animation: sequenceReverse 0.5s steps(24);
    animation-fill-mode: both;
}

#toggleMenu:not(:checked) ~ .modal {
    visibility: hidden;
    opacity: 0;
}

@keyframes sequence {
    from {
        transform: translateY(-50%) translateX(-2%);
    }
    to {
        transform: translateY(-50%) translateX(-98%);
        background-color: #000;
    }
}

@keyframes sequenceReverse {
    from {
        transform: translateY(-50%) translateX(-98%);
    }
    to {
        transform: translateY(-50%) translateX(-2%);
    }
}
