.errorWrapper-2 {
    background: url(https://media.giphy.com/media/wkW0maGDN1eSc/giphy.gif?cid=790b76110de2a179f3eb5b28e2facfab86382fb6de0a258c&rid=giphy.gif&ct=g)
        no-repeat center center fixed;
    background-size: cover;
    font-family: "Roboto", sans-serif;

    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.error-2 h1 {
    font-size: 7em;
    margin: 0.2em 0.5em;
    color: rgba(0, 0, 0, 0.9);
    margin-bottom: 0px;
}

.error-2 h2 {
    font-size: 1.2em;
    margin: 0.2em 0.5em;
    color: rgba(0, 0, 0, 0.9);
}

.error-2 {
    position: absolute;
    top: 30%;
    margin-top: -8em;
    width: 100%;
    text-align: center;
}
