.watermark {
    background: url("../images/Background-small.png");
    background-repeat: repeat !important;
    min-height: calc(100vh - 70px);
    margin-top: 5px;
    padding-top: 15px;

    .card {
        opacity: 0;
    }
}

.products-grid {
    min-height: 65vh;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 350px;
    row-gap: 32px;
    column-gap: 32px;
    margin-bottom: 10vh;
}

.card {
    margin: 0px 8px 32px 8px;
    // box-shadow: 0 24px 38px 3px rgba(0, 0, 0, 0.14),
    //     0 9px 46px 8px rgba(0, 0, 0, 0.12), 0 11px 15px -7px rgba(0, 0, 0, 0.2);
    a {
        color: black;
    }

    .card-content {
        h3 {
            font-weight: 575;
            margin-bottom: 2px;
            text-align: center;
            &:hover {
                color: #f7299a !important;
            }
        }

        p {
            font-size: 14px;
            text-align: center;
            margin: 0;
        }
    }
}

.container {
    position: relative;
    width: 90%;
    left: 5%;

    h1 {
        margin-top: 0px;
    }
}

img {
    max-width: 100%;
    max-height: 100%;
}

.card-image {
    max-width: 100%;
    max-height: 100%;
}

@media only screen and (max-width: 750px) {
    .products-grid {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 5px;
        grid-template-rows: 180px;
    }
}

@media only screen and (max-width: 1180px) and (min-width: 751px) {
    .products-grid {
        grid-template-columns: 1fr 1fr;
    }
}
