.animated-logo-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    span {
        position: relative;
        z-index: 200;
        width: 80%;
        left: 10%;
    }
}

.dc-logo {
    height: 100% !important;
    height: 25.5px !important;
}

.star-1 {
    position: absolute;
    left: -4%;
    top: -50%;
    transform: scale(0);
    z-index: 10;
}
